import React, { useState } from 'react'
import { ClientMenuGroup, ClientSuperMenuItem } from '../types'
import MenuLink from './MenuLink'

interface Props {
  menuGroup: ClientMenuGroup<ClientSuperMenuItem>
  index: number
  singleDocLeaf?: boolean
}

const MenuGroup = ({ menuGroup, index, singleDocLeaf }: Props) => {
  const [clientExpanded, setClientExpanded] = useState(menuGroup.serverExpanded)
  const [isLoading, setIsLoading] = useState(false)
  let icon

  const getMenuClass = () => {
    let menuClass = ''
    if (menuGroup.children) {
      menuClass += 'has-menu '
    }
    if (menuGroup.active) {
      menuClass += 'is-active '
    }
    if (menuGroup.children && clientExpanded) {
      menuClass += 'is-expanded'
    }
    return menuClass
  }

  if (menuGroup.children && !clientExpanded && !isLoading) {
    icon = (<div className='right-caret'></div>)
  } else if (menuGroup.children && clientExpanded && !isLoading) {
    icon = (<div className='down-caret'></div>)
  } else if (!menuGroup.url?.includes('#') && isLoading) {
    icon = (<div className='loading-spinner'></div>)
  } else {
    icon = undefined
  }

  const menuItems = (
    <>
      { clientExpanded && menuGroup.children?.map((menuItem, i) => {
        if (menuItem.type === 'menuLink' || menuItem.type === 'externalMenuLink') {
          return <MenuLink menuLink={menuItem} index={i} key={i} />
        } else if (menuItem.type === 'menuGroup') {
          return <MenuGroup menuGroup={menuItem} index={i} key={i} />
        }
      })}
    </>
  )

  if (singleDocLeaf) {
    return menuItems
  }

  return (
    <ul key={`ul-${index}`}>
      <li key={`${index}-${String(menuGroup.title)}`}>
        <a href={menuGroup.isLeafRoot ? menuGroup.path : ''} data-test-id="menu-group" key={`a-${index}`} className={getMenuClass()} onClick={(event) => {
          if (menuGroup.active || !menuGroup.isLeafRoot) {
            setClientExpanded(!clientExpanded)
            event.preventDefault()
          } else {
            setIsLoading(true)
          }
        }}>
          { menuGroup.title }
          { icon }
        </a>
        <ul>
          { menuItems }
        </ul>
      </li>
    </ul>
  )
}

export default MenuGroup
