import { createContext } from 'react'
import { SDM } from '@siliconlabs/sdm-schema'
import { ClientDocLeaf, ClientDocSpace } from '../types'

// Todo: move me
interface PageContextState {
  isStatic: boolean // If the page is being generated statically.
  relPath: string // The current relative depth of the html item (for static rendering)
  pathname: string // The current URL pathname, req.path (ssr) or windows.location.pathname (client)
  docSpace?: ClientDocSpace // The current docSpace
  docLeaf?: ClientDocLeaf // The current docLeaf
  tagSet?: SDM.TagSet
}

export const PageContext = createContext<PageContextState>({
  isStatic: false,
  relPath: '.',
  docSpace: undefined,
  docLeaf: undefined,
  tagSet: undefined,
  pathname: '',
})
