import React, { useContext } from 'react'
import { ClientDocSpaceMenu, ClientDocLeafMenu } from '../types'
import { PageContext } from '../frontend/pageContext'
import MenuLink from './MenuLink'
import MenuGroup from './MenuGroup'

interface Props {
  menu: ClientDocSpaceMenu | ClientDocLeafMenu
}

const Menu = ({ menu }: Props) => {
  const { isStatic, relPath, docSpace, docLeaf } = useContext(PageContext)

  return (
    <div className="main" id="js-menu" data-test-id="menu">
      { docSpace
        && <h3 id={ docSpace.title } data-test-id="docspace-menu-title" className="doc-object-title">
          <a href={ isStatic ? `${relPath}/index.html` : docSpace.path }>
            { docSpace.title }
          </a>
        </h3>
      }
      { !docSpace && docLeaf
        && <h3 id={ docLeaf.title } data-test-id="docleaf-menu-title" className="doc-object-title">
          <a href={ isStatic ? `${relPath}/pages/index.html` : docLeaf.path }>
            { docLeaf.title }
          </a>
        </h3>
      }
      <ul>
        {(menu.children as any).map((menuItem, i) => {
          if (menuItem.type === 'menuLink' || menuItem.type === 'externalMenuLink') {
            return <MenuLink menuLink={menuItem} index={i} key={`mlink-${i}`} />
          } else if (menuItem.type === 'menuGroup') {
            return <MenuGroup menuGroup={menuItem} index={i} key={`mgroup-${i}`} singleDocLeaf={menu.children.length === 1} />
          }
        })}
      </ul>
    </div>
  )
}
export default Menu
